.control {
    display: block;
    position: relative;
    padding-left: 44px;
    margin-bottom: 0px;
    padding-top: 6px;
    cursor: pointer;
    font-size: 24px;
}

.completed {
    text-decoration: line-through;
    color: #00000061;
}

.control input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.control_indicator {
    position: absolute;
    top: 2px;
    left: 0;
    height: 30px;
    width: 30px;
    background: #ffffff;
    border: 3px double #fe4c01;
    border-radius: 20px;
}

.control:hover input~.control_indicator,
.control input:focus~.control_indicator {
    background: none;
}

.control input:checked~.control_indicator {
    background: #ff9e16;
}

.control:hover input:not([disabled]):checked~.control_indicator,
.control input:checked:focus~.control_indicator {
    background: #ff9e16;
}

.control input:disabled~.control_indicator {
    background: #e6e6e6;
    opacity: 0.6;
    pointer-events: none;
}

.control_indicator:after {
    box-sizing: unset;
    content: '';
    position: absolute;
    display: none;
}

.control input:checked~.control_indicator:after {
    display: block;
}

.control-checkbox .control_indicator:after {
    left: 0px;
    top: 0px;
    width: 0px;
    height: 0px;
    border: solid #ffffff;
    border-width: 0 0px 0px 0;
    transform: rotate(45deg);
}

.control-checkbox input:disabled~.control_indicator:after {
    border-color: #7b7b7b;
}

.control-checkbox .control_indicator::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 4.5rem;
    height: 4.51rem;
    margin-left: -1.11rem;
    margin-top: -1.13rem;
    background: #ff9e16;
    border-radius: 3rem;
    opacity: 0.5;
    z-index: 99999;
    transform: scale(0);
}

@keyframes s-ripple {
    0% {
        transform: scale(0);
    }

    20% {
        transform: scale(1);
    }

    100% {
        opacity: 0;
        transform: scale(1);
    }
}

@keyframes s-ripple-dup {
    0% {
        transform: scale(0);
    }

    30% {
        transform: scale(1);
    }

    60% {
        transform: scale(1);
    }

    100% {
        opacity: 0;
        transform: scale(1);
    }
}

.control-checkbox input+.control_indicator::before {
    animation: s-ripple 250ms ease-out;
}

.control-checkbox input:checked+.control_indicator::before {
    animation-name: s-ripple-dup;
}